
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  background: #eee;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  font-size: 100%;
}
.list-move,
.list-leave-active,
.list-enter-active {
  transition: 500ms 200ms cubic-bezier(0.87, -0.41, 0.19, 1.44);
}
.list-enter,
.list-leave-active {
  /*transform: translate(100%, 0);*/
  height: 0;
  opacity: 0;
}



