
.todo {
  max-width: 500px;
  width: 100%;
  margin: auto;
  background: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.3) 3px 3px 15px;
}
.todo .title {
    margin-top: 0;
}
.todo .tasks {
    list-style: none;
    padding: 0;
    height: 200px;
    overflow: auto;
}
.todo-list .ui-tabs__header-items li {
    border-radius: 40px;
    transition: background 300ms ease-out;
    text-transform: none;
    height: 2.5rem;
    padding: 0 1.25rem;
    margin-right: 10px;
}
.todo-list .ui-tabs__header-items li.is-active {
      background: #ccc;
      color: #fff;
      border-bottom: none;
}
.todo-list .ui-tabs__active-tab-indicator {
    display: none;
}
.todo-list .ui-tabs__body {
    border: none;
}
.todo .ui-tabs--background-color-clear .ui-tabs__header + .ui-tabs__body {
    border: none;
}
.ui-elements {
  display: -ms-flexbox;
  display: flex;
}
.ui-elements .ui-textbox {
    -ms-flex: 1 1 auto;
        flex: 1 1 auto;
    margin-right: 20px;
}
.ui-elements .ui-textbox.error input {
      border-bottom-width: 2px;
      border-bottom-color: rgba(255, 0, 0, 0.3);
}
.ui-elements .ui-button {
    -ms-flex: 0 0 auto;
        flex: 0 0 auto;
    border-radius: 20px;
}



